var _this;
import wInput from '../../components/watch-login/watch-input.vue' //input
import wButton from '../../components/watch-login/watch-button.vue' //button
import { jumpBackPage } from '../../config/mixins.js'
import md5 from 'js-md5'
export default {
	mixins:[jumpBackPage],
	data() {
		return {
			//logo图片 base64
			logoImage: 'data:image/png;base64,',
			username:'', //用户/电话
			password:'', //密码
			isRotate: false, //是否加载旋转
		};
	},
	components:{
		wInput,
		wButton,
	},
	mounted() {
		_this= this;
		//this.isLogin();
	},
	methods: {
		isLogin(){
			//判断缓存中是否登录过，直接登录
			// try {
			// 	const value = uni.getStorageSync('setUserData');
			// 	if (value) {
			// 		//有登录信息
			// 		console.log("已登录用户：",value);
			// 		_this.$store.dispatch("setUserData",value); //存入状态
			// 		uni.reLaunch({
			// 			url: '../../../pages/index',
			// 		});
			// 	}
			// } catch (e) {
			// 	// error
			// }
		},
	    startLogin(){
			//登录
			if(this.isRotate){
				//判断是否加载中，避免重复点击请求
				return false;
			}
			if (this.username.length == "") {
			     uni.showToast({
			        icon: 'none',
					position: 'bottom',
			        title: '用户名不能为空'
			    });
			    return;
			}
	        if (this.password.length < 5) {
	            uni.showToast({
	                icon: 'none',
					position: 'bottom',
	                title: '密码不正确'
	            });
	            return;
	        }
			var _data = {
				username: this.username,
				password: md5(md5(this.password) + 'jiehai')  //加密密码
			}
			_this.isRotate=true
			//登录 post
			this.$api.userLogin(_data,res=>{
				_this.isRotate=false
				if(res.code == 0){//登陆成功
					uni.showToast({
					    title: '登陆成功'
					});
					_this.$db.set('userToken',res.token)
					_this.$db.set('userInfo',res.data)
					_this.handleBack()
					
				}else{
					uni.showToast({
					    title: '服务异常'
					});
				}
			})
	    },
		login_weixin() {
			//微信登录
			uni.showToast({
				icon: 'none',
				position: 'bottom',
				title: '...'
			});
		},
		login_weibo() {
			//微博登录
			uni.showToast({
				icon: 'none',
				position: 'bottom',
				title: '...'
			});
		},
		login_github() {
			//github登录
			uni.showToast({
				icon: 'none',
				position: 'bottom',
				title: '...'
			});
		}
	}
}
